.o-login-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

}

.o-logo-login{
    width: 12em;
}

.o-text-login{
    font-size: 1em;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: normal;
    color: black;
    width: 90%;
}

.o-form-login{
    margin: 2em 0;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#input-identification{
    margin: 1em 0;
    height: 1.5em;
    width: 100%;
    text-align: center;
}

#submit{
    width: 50%;
    background-color: #13A538;
    color: white;
    font-weight: bolder;
    border-radius: 7px;
    height: 2.5em;
}
